// This module does the handling for the metrics context
// activity event metadata.

define('client/lib/metricsContext',[], function () {
  'use strict';

  return {
    marshall: function (data) {
      return {
        flowId: data.flowId,
        flowBeginTime: data.flowBeginTime
      };
    }
  };
});

